.card-movie {
     /* width: calc((100vw / 4) - 1.2rem); */

     padding: 1rem;
     display: flex;
     flex-direction: column;
     align-items: center;
     gap: 1.2rem;
     font-size: 1.2rem;
     font-weight: bolder;
}

.card-movie:hover {
     cursor: pointer;
     transform: scale(1.1);
     -webkit-transform: scale(1.1);
     -moz-transform: scale(1.1);
     -ms-transform: scale(1.1);
     -o-transform: scale(1.1);
}
.image {
     width: 200px;

}

.not-image {
     width: 200px;
     display: flex;
     text-align: center;
     justify-content: center;
     align-items: center;
     height: 300px;
     background-color: #fff;
     color: #000;
}